import React from 'react';
import styles from '../components/information.module.css';
import CopyButton from '../components/copyButton';

const UrlCart = ({linkData}) => {
    const {originalLink, shortLink, expireDate, qrLink} = linkData;
    const expireDateObject = expireDate && new Date(expireDate * 1000);
    return (
        <div className={styles.common}>
            <div className={styles.row}>
                <div>
                    <div className={styles.shorted}>
                        <span>{shortLink}</span>
                        <CopyButton copyString={shortLink}/>
                    </div>
                    <div className={styles.urlValue}>
                        <span>{originalLink}</span>
                    </div>
                </div>
                <div className={styles.qrblock}>
                    <img alt="qr-code" className={styles.qrcode} src={qrLink}/>
                    <CopyButton copyString={qrLink}/>
                </div>
            </div>
            <div className={styles.expire}>
                <span>Expired:  </span>
                <span>{expireDateObject.toGMTString()}</span>
            </div>
        </div>
    );
};

export default UrlCart;
