import React, {Component} from 'react';
import {SIGNUP_URL} from "../helpers/constants";
import styles from '../components/information.module.css';

class SpecialOffer extends Component {

    subscribe = () => window.Pushwoosh.subscribe();

    signUp = () => window.location.href = SIGNUP_URL;

    render() {
        const {isSubscribed} = this.props;
        return(
            <div className={styles.offer}>
                {isSubscribed !== null && isSubscribed ? this.renderSignupButton() : this.renderSubscribeButton()}
            </div>
        )
    }

    renderSubscribeButton() {
        return(
            [
                <span key="subscribe-span">We will inform you about the statistics</span>,
                <button key="subscribe-button" className={styles.btn} onClick={this.subscribe}>Get notified</button>
            ]
        )
    }

    renderSignupButton() {
        return(
            [
                <span key="signup-span">Want to manage, share, and track your links?</span>,
                <button key="signup-button" className={styles.btn} onClick={this.signUp}>Sign UP</button>
            ]
        )
    }
}

export default SpecialOffer;